.outerDiv{
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60vw;
  height:90vh;
  border-radius: 30px;
  background-color: rgb(28, 177, 247);
}
.todo-app-div{
  width: 100%;
  height: 100%;
}
.heading{
  width: 100%;
  height: 10vh;
  border-radius: 30px 30px 0 0;
  background-color: rgb(47, 115, 218);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  color: white;
  font-weight: bold;
  text-shadow: 0.1em 0.2em  rgb(109, 109, 109);
}

.list-box{
  margin-top: 20px;
  width: 100%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center; 
}
.input-area{
  margin-bottom:5vh;
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
}
.input-area > input{
 margin: 0 2vw 0 3vw;
 width: 100%;
 height: 5vh;
 border: 2px solid black;
 border-radius: 30px;
 padding: 0 10px 0 10px;
 font-size: 1.5rem;
 font-weight: bold;
}
input::after{
  border: none;
}
.input-area > .add{
  cursor: pointer;
  font-size: 3rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid grey;
  border-radius: 50%;
  height: 5vh;
  padding: 0.5vw;
  color: white;
  background-color: rgb(171, 75, 190);
  transition: all 0.2s ease-in-out;
}
.my{
  -ms-overflow-style: none;
  scrollbar-width: none;
  overflow-y: scroll;
  height: 65vh;
  width: 100%;
  display: flex;
  flex-direction: column; 
  align-items: center; 
}
.my::-webkit-scrollbar{
  display: none;
}
.list-items{
  display: flex;
  margin-bottom: 2vh; 
  flex-direction: row; 
  align-items: center; 
  justify-content:space-between;
  background-color:rgba(255, 255, 255, 0.4);
  /* background-color: rgb(241, 241, 241); */
  padding: 5px;
  border-radius: 30px;
  width: 100%;
  height: auto;
}
.list-items>div{
  /* overflow-y: scroll; */
  max-height: fit-content;
  color: black;
  display: flex;
  width: 90%;
  justify-content: center;
  align-items: center;
  text-align:center;
  font-size: 2vw;
  height: 100%;
}
.list-items>button{
  right: 0;
  font-size: 3rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid orange;
  border-radius: 50%;
  margin-right: 1vw;
  height: 5vh;
  padding: 0.5vw;
  color: white;
  background-color: orange;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
.list-items>button:hover{
  background-color: red;
  transition: background-color 0.2s ease-in-out;
}
.input-area>.add:hover{
  background-color: green;
  transition: background-color 0.2s ease-in-out;
}


@media screen and (max-width:600px) {
  .outerDiv{
      width: 90vw;
  }
  .list-items>div{
      overflow-x: scroll;
      font-size: 1.5rem;
  }
}